import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD, TZB_HTTP_CUS1, TZB_HTTP_CUS2 } from '../../../../../../../@uisftech/common/service/http.constant';
import * as $ from 'jquery';
import { MarketingHttpService } from '../../../http/marketing.http.service';
import { API } from 'app/pages/tzb/custom/marketing/http/marketing.constant';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import * as _ from 'lodash';
/**
 * 文件导入
 */
@Component({
	selector: 'file-import',
	templateUrl: './file-import.component.html',
	styleUrls: ['./file-import.component.scss'],
	providers: [MarketingHttpService]
})
export class FileImport implements OnInit, OnChanges {
	@Input() inValue: any;
	@Output() outValue: EventEmitter<any> = new EventEmitter();
	fileDisplay: boolean = false;
	dataList: any = [];
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;
	UPLOAD: string = TZB_CUS_UPLOAD;
	loadObj: any = {};
	// 表格中显示的信息
	tabMesg: any = tableMessage;
	TZB_HTTP_CUS1 = TZB_HTTP_CUS1;
	msgs: Message[] = [];
	dtId: string;//模板编号
	marketingCampaignId: string;
	examplesList: any = [];
	TZB_HTTP_CUS2 = TZB_HTTP_CUS2;
	dtStatusId: any = [];
	code: any;
	inValue2: any;
	marketingCampaigntType: string;//任务类型
	showDisabled: boolean = false;//可编辑
	deId: string;
	fileName: string;
	Property: boolean = true;
	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public marketingHttpService: MarketingHttpService,
		public commfunc: Commfunc,
	) {
		this.codeValues() //调用方法，获取全部码值
		this.dtStatusId = this.code['LivingStatusId'];//实例的状态
	}

	ngOnInit() {
		setTimeout(() => {
			this.examplesList = [];
		})
		this.marketingCampaignId = this.inValue.marketingCampaignId;
		this.marketingCampaigntType = this.inValue.marketingCampaigntType;//任务类型
		this.queryMarketingCampaignById()
		this.findDocumentTemplate();
	}
	ngOnChanges() {
		setTimeout(() => {
			this.examplesList = [];
		})
		this.marketingCampaignId = this.inValue.marketingCampaignId;
		this.marketingCampaigntType = this.inValue.marketingCampaigntType;//任务类型
		this.queryMarketingCampaignById()
		this.findDocumentTemplate();
		console.log(this.inValue);
		if (this.inValue.marketingObjectType && this.inValue.marketingObjectType == '04') {
			this.Property =  false;
		}
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	findDocumentTemplate() {
		let param = {
			fileTemplateSub: '',
			fileTemplateCate: this.marketingCampaigntType,//主类型是根据任务类型进行变化
		}
		this.marketingHttpService.findDocumentTemplate(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dtId = data.templateModleList[0].dtId;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//模板下载
	modelDownload() {
		this.marketingHttpService.documentDownload({ dtId: this.dtId }).subscribe(data => {
			if (data.returnCode.code == "success") {
				window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl, "_self")
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		})
	}
	toDetail(item) {
		this.fileDisplay = true;
		this.inValue2 = { marketingCampaignId: this.marketingCampaignId, marketingCampaigntType: this.marketingCampaigntType };

	}
	//文件导入-导入按钮
	loadAfter(event) {
		if (event.xhr.response) {
			if (JSON.parse(event.xhr.response).success == false) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
				return
			} else {
				let arr = {};
				arr['fileName'] = JSON.parse(event.xhr.response)[0].fileName;
				arr['fileUrl'] = JSON.parse(event.xhr.response)[0].fileUrl;
				this.loadObj = arr;
				// let sendParam = {
				// 	marketingObjectFileMap: this.loadObj,
				// }
				// this.outValue.emit(sendParam);
				let param = {
					dtId: this.dtId,
					marketingCampaignId: this.marketingCampaignId,
					deFilename: JSON.parse(event.xhr.response)[0].fileName,
					deFileurl: JSON.parse(event.xhr.response)[0].fileUrl
				}
				let params = this.commfunc.handleParams(param)
				let TZB_HTTP_CUS2 = this.TZB_HTTP_CUS2 + API.doucumentDataDeal;
				let _self = this;
				$.ajax({
					url: TZB_HTTP_CUS2,
					type: 'POST',
					data: JSON.stringify(params),
					async: false,
					headers: {
						'x-ajax': '1',
						'Content-Type': 'application/json'
					},
					dataType: 'json',
					success: function (data) {
						if (data.returnCode.code == 'success') {
							_self.showDisabled = true;
							_self.findDocumentExamples(data.deId, data.contentId);
						} else {
							_self.showDisabled = false;
							_self.msgs = [];
							_self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
						}
					},
					error: function (err) {
						console.log(err);
					}
				})
			}
		}
	}

	//文件导入回显列表
	findDocumentExamples(item, items) {
		this.commfunc.setSessionDataCH('deId', item);
		if (items != '') {
			this.commfunc.setSessionDataCH('contentId', items);
		}
		let param = {
			deId: item
		}
		this.marketingHttpService.findDocumentExamples(param).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.examplesList = data.examplesList;
				this.outValue.emit('1');
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})

	}
	//调详情接口拿deId
	queryMarketingCampaignById() {
		this.marketingHttpService.queryMarketingCampaignById({ marketingCampaignId: this.marketingCampaignId }).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.deId = data.deId;
				if (this.deId) {
					this.showDisabled = true;
					this.findDocumentExamples(this.deId, '');
				} else {
					this.showDisabled = false;
				}
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
	//列表删除
	toShanChu() {
		if (this.commfunc.getSessionDataCH('deId')) {
			this.marketingHttpService.campaignCustDataDelete({ marketingCampaignId: this.marketingCampaignId, contentId: this.commfunc.getSessionDataCH('contentId') }).subscribe(data => {
				if (data.returnCode.code == "success") {
					this.examplesList = [];
					this.loadObj.fileName = '';
					this.commfunc.clearSession('deId')
					this.showDisabled = false;
					this.outValue.emit('0');
					// this.commfunc.setSessionDataCH('deId', data.deId);

				} else {
					this.msgs = [];
					this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
				}
			}, error => {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
			})
		}
	}
}
