import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { MarketingHttpService } from '../../../http/marketing.http.service';

/**
 * 文件导入
 */
@Component({
	selector: 'file-import-detail',
	templateUrl: './file-import-detail.component.html',
	styleUrls: ['./file-import-detail.component.scss'],
	providers: [MarketingHttpService]
})
export class FileImportDetail implements OnInit {
	msgs: any[];
	dataList: any = [];
	@Input() inValue: any;
	marketingCampaignId: string;
	marketingCampaigntType: string;//任务类型
	pageNum: number;
	countNum: number;
	item: any = {};
	first: number = 0;//分页控制
	tableHeader: any = [];//字段头
	progress: boolean = false;
	// 表格中显示的信息
	tabMesg: any = tableMessage;
	totalNum:number;
	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public marketingHttpService: MarketingHttpService,
		public commfunc: Commfunc,
	) {
	}

	ngOnInit() {
		this.marketingCampaignId = this.inValue.marketingCampaignId;
		this.marketingCampaigntType = this.inValue.marketingCampaigntType;//任务类型
		this.item['countNum'] = 10;
		this.item['pageNum'] = 1;
		this.queryClick();

	}
	//客户名单详细查询
	queryClick() {
		this.item['marketingCampaignId'] = this.marketingCampaignId;
		this.marketingHttpService.queryCustomerByTopicId(this.item).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.tableHeader = data.fieldMap;
				this.dataList = data.customerList;
				this.totalNum=data.totalNum;
				if (data.totalNum == 0) {
					this.progress = true;

				} else {
					this.progress = false;
				}
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//分页
	evaluatePage(event) {
		//每页显示的条数
		this.item['countNum'] = event.rows * 1;
		//当前页
		let currentPage = event.page + 1;
		this.item['pageNum'] = currentPage;
		this.first = (currentPage - 1) * this.item['countNum'];
		this.queryClick();
	}
}
