import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message, ConfirmationService } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { QueryPlanBean } from '../../marketing-project/query-plan/bean/QueryPlan.bean';
import * as $ from 'jquery';
import { MarketingHttpService } from '../../http/marketing.http.service';
import * as moment from 'moment';
import * as _ from 'lodash';
import { MarketingObjBean } from './bean/marketing-object.bean';
import { percent, checkZzzNum } from '../../../../../../@uisftech/common/service/validator';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
/**
 * 营销对象
 */
@Component({
	selector: 'marketing-object',
	templateUrl: './marketing-object.component.html',
	styleUrls: ['./marketing-object.component.scss'],
	providers: [MarketingHttpService, Commfunc, ConfirmationService]
})
export class MarketingObjComponent implements OnInit {
	fileDisplay: boolean = false;
	marketingObjBean: MarketingObjBean = new MarketingObjBean();
	marketingCampaignId: string;
	msgs: Message[] = [];
	isFirstStep: boolean = false;
	ch: any = CALENDAR_CN;
	marketingCampaigntType: string;
	inValue3: any;
	outValue: any;
	activiteData: any = {};//接收详情的数据
	flagCopy: string;
	url: any = [];
	isNext: boolean = false;
	dataList: any = [];
	deId: string;//实例编号
	progress: boolean = false;
	item: any = {};
	first: number = 0;//分页控制
	totalCount: number;
	dt: Date = null//数据日期
	inValue2: any;//大数据平台筛选传值
	resultList: any = [];
	dataObj = {};//接收表格中的数据
	Property1: boolean = false;
	isShowTip: boolean = true;
	//校验
	userform: FormGroup;
	marketingObjectType: string;
	exploreCondtionId: string;
	marketingObjectType04: boolean = false;
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public activatedRoute: ActivatedRoute,
		public marketingHttpService: MarketingHttpService,
		public confirmationService: ConfirmationService,
		public fb: FormBuilder,
	) {
		if (this.activatedRoute.snapshot.params['marketingCampaignId']) {
			this.marketingCampaignId = this.activatedRoute.snapshot.params['marketingCampaignId'];//营销活动id
		}
		this.flagCopy = this.activatedRoute.snapshot.params['flagCopy'];
		this.marketingCampaigntType = this.activatedRoute.snapshot.params['marketingCampaigntType'];//任务类型
		this.marketingObjBean.exploreCondtionId = this.activatedRoute.snapshot.params['expCondId'];
		//flagCopy='1'-复制
		//flagCopy='2'-修改
		if (this.activatedRoute.snapshot.params['relationProductList']) {
			this.dataList = JSON.parse(this.activatedRoute.snapshot.params['relationProductList']);
		}
		if (this.commfunc.getSessionDataCH('marketingParam')) {
			this.marketingObjectType = JSON.parse(this.commfunc.getSessionDataCH('marketingParam')).marketingObjectType;
			console.log(this.marketingObjectType);
			this.exploreCondtionId = JSON.parse(this.commfunc.getSessionDataCH('marketingParam')).exploreCondtionId;
		}
		this.inValue3 = { marketingCampaignId: this.marketingCampaignId, marketingCampaigntType: this.marketingCampaigntType, marketingObjectType: this.marketingObjectType }

	}

	ngOnInit() {
		this.outValue = '0';
		// this.queryMarketingCampaignById();
		debugger
		if (this.marketingObjBean.exploreCondtionId) {
			this.marketingObjBean.marketingObjectType = '01';//高级搜索
			this.marketingObjBean.marketingCampaignId = this.marketingCampaignId;
			// this.isFirstStep = true;
		}
		//  else if (9){
		// 	this.marketingObjBean.marketingObjectType = '02';//文件导入
		// 	this.addSerch('02');

		// }
		else if (this.marketingObjectType == '03') {
			this.marketingObjBean.marketingObjectType = '03';//大数据
			this.addSerch('03');
		} else if (this.marketingObjectType == '01') {
			this.marketingObjBean.marketingObjectType = '01';//高级搜索
			this.marketingObjBean.exploreCondtionId = this.exploreCondtionId
		} else if (this.marketingObjectType == '04') {
			this.marketingObjBean.marketingObjectType = '04';//批量激活
			this.marketingObjectType04 = true;
			this.addSerch('04');
		} else {
			this.marketingObjBean.marketingObjectType = '02';//文件导入
			this.addSerch('02');
		}
		this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
		this.url.forEach((element, index) => {
			if (element.label == "MarketingObjComponent") {
				if (index + 1 == this.url.length) {//判断是最后一个
					this.isNext = true;
				}
			}
		});
		this.userform = this.fb.group({
			"releasePersent": new FormControl('', [Validators.pattern(/^(100|[1-9]?\d(\.\d\d?\d?)?)$/)]),
			"releaseQuantity": new FormControl('', [Validators.pattern(/^[0-9]\d*$/)]),
		});
	}

	//高级搜索
	addSerch(item) {
		if (item == '02') {//文件导入
			this.inValue3 = { marketingCampaignId: this.marketingCampaignId, marketingCampaigntType: this.marketingCampaigntType }
		} else if (item == '03') {//大数据
			this.item['pageSize'] = 10;
			this.item['pageNum'] = 1;
			this.queryClick();
			this.echoClick();
			// this.queryMarketingCampaignById();
		} else if (item == '01') {//高级搜索
			this.queryMarketingCampaignById();
		} else if (item == '04') {//批量激活

		}
	}
	sign(item) {
		this.outValue = item;
	}
	//详情
	echoClick() {
		this.marketingHttpService.queryMarketingCampaignById({ marketingCampaignId: this.marketingCampaignId }).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.marketingObjBean.releasePersent = data.releasePersent;
				this.marketingObjBean.releaseQuantity = data.releaseQuantity;
				// if (data.pushDate) {
				// 	this.dt=new Date(data.pushDate);
				// }

			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
	//上一步
	prev() {
		let param = {};
		if (this.flagCopy) {//复制或者是修改
			param = {
				marketingCampaignId: this.marketingCampaignId,
				marketingCampaigntType: this.marketingCampaigntType,
				relationProductList: JSON.stringify(this.dataList),
				flagCopy: this.flagCopy,
				tabPrev: 'tabUp'
			}
		} else {
			param = {
				marketingCampaignId: this.marketingCampaignId,
				marketingCampaigntType: this.marketingCampaigntType,
				relationProductList: JSON.stringify(this.dataList),
				tabPrev: 'tabUp'
			}
		}

		this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
		let navUrl = '';
		let indexId = '';
		this.url.forEach((element, index) => {
			if (element.label == "MarketingObjComponent") {
				navUrl = this.url[index - 1].url;
				indexId = this.url[index - 1].index;
			}
		});
		this.router.navigate([navUrl, param]);//基本要素
		this.commfunc.setSessionDataCH('index', indexId)
	}
	//下一步
	next() {
		let param = {};
		// param = {
		// 	marketingCampaignId: this.marketingCampaignId,
		// 	marketingCampaigntType: this.marketingCampaigntType,
		// 	relationProductList: JSON.stringify(this.dataList),
		// }
		if (this.flagCopy) {
			param = {
				marketingCampaignId: this.marketingCampaignId,
				relationProductList: JSON.stringify(this.dataList),
				marketingCampaigntType: this.marketingCampaigntType,
				// activiteData: JSON.stringify(this.activiteData),
				flagCopy: this.flagCopy,
			}
		} else {
			param = {
				marketingCampaignId: this.marketingCampaignId,
				marketingCampaigntType: this.marketingCampaigntType,
				relationProductList: JSON.stringify(this.dataList),
			}
		}
		if (this.marketingObjBean.marketingObjectType == '02') {//文件导入\批量激活
			if (this.marketingObjBean.marketingObjectType == '02' && !this.commfunc.getSessionDataCH('deId')) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '请导入文件' });
				return;
			}
			this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
			let navUrl = '';
			let indexId = '';
			this.url.forEach((element, index) => {
				if (element.label == "MarketingObjComponent") {
					navUrl = this.url[index + 1].url;
					indexId = this.url[index + 1].index;
				}
			});
			this.router.navigate([navUrl, param])//关联产品
			this.commfunc.setSessionDataCH('index', indexId)
		} else {//大数据和高级搜索
			if (this.marketingObjBean.marketingObjectType == '03') {//大数据
				this.isShowTip = true;
				//校验的
				for (const i in this.userform.controls) {
					this.userform.controls[i].markAsDirty();
				}
				//处理是否提交
				for (const i in this.userform.controls) {
					if (!this.userform.controls[i].valid) {
						return;
					}
				}
				this.marketingObjBean.marketingCampaignId = this.marketingCampaignId;
				this.marketingObjBean.pushDate = this.dataObj['dt'];
				if (this.marketingObjBean.releaseQuantity && this.marketingObjBean.releasePersent) {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '数据型，手动录入，发布比例和发布数量两者互斥，选其一' });
					return
				}
				if (this.marketingObjBean.marketingObjectType == '03' && !this.dataObj['dt']) {//大数据平台筛选
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择一条数据' });
					return
				}
			} else {
				this.marketingObjBean.marketingCampaignId = this.marketingCampaignId;
			}

			this.marketingHttpService.createCampaignCust(this.marketingObjBean).subscribe(data => {
				if (data.returnCode.code == 'success') {
					// this.commfunc.clearSession('marketingParam')
					this.isShowTip = false;
					let paramSum = {
						marketingCampaignId: this.marketingCampaignId,
						type: 'marking',
						marketingCampaigntType: this.marketingCampaigntType,
					}
					this.deId = data.deId;
					if (this.deId) {
						this.commfunc.setSessionDataCH('deId', this.deId);
					}
					if (this.isNext) {//判断是最后一个
						this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/marketing-activity-detail', paramSum])
					} else {
						this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
						let navUrl = '';
						let indexId = '';
						this.url.forEach((element, index) => {
							if (element.label == "MarketingObjComponent") {
								navUrl = this.url[index + 1].url;
								indexId = this.url[index + 1].index;
							}
						});
						this.router.navigate([navUrl, param])//关联产品
						this.commfunc.setSessionDataCH('index', indexId)
					}

				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
			})

		}
	}
	// 高级搜索-请确认是否导入该搜索结果?
	// breakcast() {
	// 	this.isFirstStep = false;
	// }
	//重置
	rest() {
		if (this.marketingObjBean.marketingObjectType == '03') {
			// this.num=1;
			this.isShowTip = false;
			this.marketingObjBean.releasePersent = '';
			this.marketingObjBean.releaseQuantity = '';
			this.dt = null;
		} else {
			this.marketingObjBean.exploreCondtionId = ''
		}

	}
	//大数据平台筛选详情
	toDetail(item) {
		this.fileDisplay = true;
		this.inValue2 = { dt: item.dt, marketingCampaignId: this.marketingCampaignId };
	}
	//调详情接口拿deId
	queryMarketingCampaignById() {
		this.marketingHttpService.queryMarketingCampaignById({ marketingCampaignId: this.marketingCampaignId }).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.deId = data.deId;
				if (this.marketingObjBean.marketingObjectType = '01') {
					let param = {
						marketingCampaignId: this.marketingCampaignId,//类型判断是高级搜索
						marketingCampaigntType: this.marketingCampaigntType,
						deId: data.deId
					}
					this.router.navigate(['pages/tzb/custom/custom-information/advanced-search/add-search', param]); //跳转到高级搜索
				}
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
	//大数据查询接口
	queryClick() {
		this.item['pageSize'] = 10;
		this.item['pageNum'] = 1;
		this.first = 0;
		this.query()
	}
	query() {
		this.item['marketingCampaignId'] = this.marketingCampaignId;
		this.item['dt'] = this.commfunc.transDateN(this.dt)
		this.marketingHttpService.getBigDataStatisticsList(this.item).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.totalCount = data.totalNum;
				this.resultList = data.resultList;
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
	paginate(event) {
		//每页显示的条数
		this.item['pageSize'] = event.rows * 1;
		//当前页
		let currentPage = event.page + 1;
		this.item['pageNum'] = currentPage;
		this.first = (currentPage - 1) * this.item['pageSize'];
		this.query()
	}
	//选择一条数据
	checkClick2(item) {
		this.dataObj = item;
	}
}
