import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Equhundered, checkZzNum } from '../../../../../../../@uisftech/common/service/validator';
import { MarketingHttpService } from '../../../http/marketing.http.service';
/**
 * 大数据平台筛选详情
 */
@Component({
	selector: 'big-data-plat',
	templateUrl: './big-data-plat.component.html',
	styleUrls: ['./big-data-plat.component.scss'],
})
export class BigDataPlat implements OnInit {
	@Input() inValue: any;
	item: any = {};
	marketingCampaignId: string;
	dt: string;
	tableHeader:any=[];
	dataList:any=[];
	totalNum:number;
	msgs:any[];
	first: number = 0;//分页控制
	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public marketingHttpService: MarketingHttpService,
		public commfunc: Commfunc,
	) {

	}

	ngOnInit() {
		this.marketingCampaignId = this.inValue.marketingCampaignId;
		this.dt = this.inValue.dt;
		this.item['pageSize'] = 10;
		this.item['pageNum'] = 1;
		this.queryClick();
	}
	queryClick() {
		this.item['marketingCampaignId'] = this.marketingCampaignId;
		this.item['dt'] = this.dt;
		this.marketingHttpService.getBigDataStatisticsDetail(this.item).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.tableHeader = data.fieldList;
				this.dataList = data.resultList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//分页
	evaluatePage(event) {
		//每页显示的条数
		this.item['pageSize'] = event.rows * 1;
		//当前页
		let currentPage = event.page + 1;
		this.item['pageNum'] = currentPage;
		this.first = (currentPage - 1) * this.item['pageSize'];
		this.queryClick();
	}

}
